* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #3e3742;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  display: block;
}

.search-form {
  position: relative;
}

input {
  width: 300px;
  color: #fff;
  background-color: #a341db;
  border: 2px solid rgba(64, 18, 90, .604);
  border-radius: 6px;
  padding: 5px;
  font-size: 16px;
  font-style: italic;
}

input::-ms-input-placeholder {
  color: rgba(252, 252, 252, .753);
}

input::placeholder {
  color: rgba(252, 252, 252, .753);
}

.search {
  border: 2px solid rgba(64, 18, 90, .604);
  border-left: none;
  border-top-right-radius: 6px;
  border-bottom-right-radius: 6px;
  padding: 5px 10px;
  position: absolute;
  top: 0;
  right: 0;
}

.container {
  z-index: 499;
  width: 100%;
  background-color: rgba(62, 55, 66, .514);
  justify-content: center;
  align-items: center;
  margin-bottom: 15px;
  padding: 10px;
  display: flex;
  position: fixed;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  padding-top: 55px;
  display: flex;
}

.photo-card {
  background-color: rgba(81, 15, 107, .376);
  border-radius: 10px;
  transition: transform .3s;
}

.photo-card:hover {
  transform: scale(1.03);
}

.info {
  background: linear-gradient(rgba(81, 15, 107, .808), rgba(142, 132, 238, .8));
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  justify-content: center;
  padding: 5px;
  display: flex;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.info-item {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
  font-weight: 500;
  display: flex;
}

.info-item + .info-item {
  margin-left: 20px;
}

.box {
  justify-content: center;
  padding: 15px;
  display: flex;
}

.load-more.is-hidden {
  display: none;
}

.load-more {
  width: 25px;
  height: 25px;
  background-color: #3e3742;
  border: 6px solid #3e3742;
  border-top-color: #ddb4f8;
  border-radius: 50%;
  animation: spin .5s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

